import React from 'react'
import "./Sidebar.css";
import PersonAddIcon from '@material-ui/icons/PersonAdd';
import PeopleIcon from '@material-ui/icons/People';
import PieChartIcon from '@material-ui/icons/PieChart';
import InfoIcon from '@material-ui/icons/Info';
import { Link } from "react-router-dom";


export default function Sidebar() {
    return (
        <div className="sidebar">
            <div className="sidebarcontents">
                <div className="Menu">
                    <h3 className="Sidebar-heading">Dashboard</h3>
                    <ul className="Sidebar-lists">
                        <Link className="sidebaritems" to ="/NewSubscriber">
                            <PersonAddIcon className="sidebaritems-icon"/>
                            Register Subscriber
                        </Link>

                        <Link className="sidebaritems" to ="/">
                        
                            <PieChartIcon className="sidebaritems-icon"/>
                            Overview
                        
                        </Link>


                        <Link className="sidebaritems" to ="/Subscribers">
                            <PeopleIcon className="sidebaritems-icon"/>
                            Subscribers
                        </Link>

                        <Link className="sidebaritems" to ="/UserInfo/1">
                            <InfoIcon className="sidebaritems-icon"/>
                            User Subscription Info
                        </Link>
                        
                    </ul>

                </div>
            </div>
        </div>
    )
}