import "./LineChart.css";
import {
  LineChart,
  Line,
  XAxis,
  CartesianGrid,
  Tooltip,
  ResponsiveContainer,
} from "recharts";

export default function Chart() {
    const data = [
        { name: "Plan 1", values: 33 },
        { name: "Plan 2", values: 33 },
        { name: "Plan 3", values: 33 },
        { name: "Plan 4", values: 0 },
        { name: "Plan 5", values: 0 },
        { name: "Plan 6", values: 33 },
        { name: "Plan 7", values: 0 },
        { name: "Plan 8", values: 0 },
        { name: "Plan 9", values: 0 },
        { name: "Plan 10", values: 0 },
        { name: "Plan 11", values: 0 },
        { name: "Plan 12", values: 27  },
        { name: "Plan Unlimitied", values: 33 },
        
      ];
      
  return (
    <div className="Linechart">
        <div className="wtf">
            <h2 className="chartTitle">Package Plans</h2>
        </div>

            <ResponsiveContainer width="100%" aspect={4 / 1}>
                <LineChart data={data}>
                <XAxis dataKey="name" stroke="#2eb3c3" />
                <Line type="monotone" dataKey="values" stroke="#2eb3c3" />
                <Tooltip />
                <CartesianGrid/>
                </LineChart>
            </ResponsiveContainer>

     
    </div>
  );
}