import "./Register.css";


export default function Register() {
    return (
        <div className="Register">
            <h2 className="NewSubscriberHeading">New Subscriber</h2>
            <form className="NewSubscriberForm">
                <div className="FormItems">
                    <label>First Name</label>
                    <input type="text" placeholder="Joey"/>
                </div>

                <div className="FormItems">
                    <label>Middle Name</label>
                    <input type="text" placeholder="O"/>
                </div>

                <div className="FormItems">
                    <label>Last Name</label>
                    <input type="text" placeholder="Tribbiani"/>
                </div>

                <div className="FormItems">
                    <label>Username</label>
                    <input type="text" placeholder="jotnv2232e"/>
                </div>

                <div className="FormItems">
                    <label>Password</label>
                    <input type="password" placeholder="password"/>
                </div>

                <div className="FormItems">
                    <label>Email</label>
                    <input type="Email" placeholder="joeytribbiani@gmail.com"/>
                </div>

                <div className="FormItems">
                    <label>Status</label>
                    <input type="number" placeholder="0"/>
                </div>

                <div className="FormItems">
                    <label>Address</label>
                    <input type="Address" placeholder="7561, Newyork"/>
                </div>

                <div className="FormItems">
                    <label>Country</label>
                    <input type="Country" placeholder="USA"/>
                </div>

                <div className="FormItems">
                    <label>Joined Date</label>
                    <input type="Date" placeholder="1493253530"/>
                </div>

                <button className="RegisterNewSubscriber">
                    Register
                </button>
            </form>
        </div>
    )
}