import "./Information.css"
import SentimentVerySatisfiedIcon from '@material-ui/icons/SentimentVerySatisfied';
import SentimentVeryDissatisfiedIcon from '@material-ui/icons/SentimentVeryDissatisfied';
import SubscriptionsIcon from '@material-ui/icons/Subscriptions';

export default function Information() {
    return (
        <div className="Info">
            <div className="InfoItem">
                <span className="InfoTitle">Total Subscribers</span>
                <div className="InfoSubscribers">
                    <SubscriptionsIcon className="icons"/> 500
                </div>

            </div>
            <div className="InfoItem">
                <span className="InfoTitle">Active users</span>
                <div className="InfoSubscribers">
                  <SentimentVerySatisfiedIcon className="icons"/> 240
                </div>

            </div>
            <div className="InfoItem">
                <span className="InfoTitle">Non-Active users</span>
                <div className="InfoSubscribers">
                    <SentimentVeryDissatisfiedIcon className="icons"/>260
                </div>

            </div>
        </div>
    )
}